
/* src/App.css */
.ant-menu-item-selected, 
.ant-menu-item:hover, 
.ant-menu-item-active {
  color: #FED86B !important; /* 글자 색상을 노란색으로 변경 */
}

.ant-menu-item-selected::after, 
.ant-menu-item:hover::after, 
.ant-menu-item-active::after {
  border-bottom: 3px solid #FED86B !important; /* 밑줄 색상을 노란색으로 변경 */
}

/* 오버플로우 버튼 색상 변경 */
.ant-menu-horizontal > .ant-menu-item-hidden,
.ant-menu-horizontal > .ant-menu-overflowed-indicator,
.ant-menu-horizontal > .ant-menu-overflowed-indicator:hover {
  color: #FED86B !important; /* 버튼 색상 변경 */
  border-bottom: 3px solid #FED86B !important; /* 하단 바 색상 변경 */
}